/* http://prismjs.com/download.html?themes=prism&languages=markup+css+clike+javascript&plugins=normalize-whitespace */
/**
 * prism.js default theme for JavaScript, CSS and HTML
 * Based on dabblet (http://dabblet.com)
 * @author Lea Verou
 */

$color-accent-lighter: #9f6ef6;
$color-gray: #827b7b;
$color-gray-darkest: #0d0c0c;
$color-gray-lightest: #f9f8f8;
$color-gray-plus2: #4e4a4a;
$color-secondary-lightest: #cffcf6;
$color-secondary-darker: #0cbea5;
$color-primary: #0f9dee;
$color-warning: #ffc300;
$color-error-lighter: #e7525f;

$font-family-monospace: 'Brand Monospace', Monaco, Consolas, 'Lucida Console', monospace;


code[class*="language-"],
pre[class*="language-"] {
	color: $color-gray-darkest;
	background: none;
	// text-shadow: 0 1px white;
	font-family: font-family(monospace);
	text-align: left;
	white-space: pre-wrap;
	word-spacing: normal;
	word-break: normal;
	word-wrap: normal;
	line-height: 1.5;

	tab-size: 0;
	hyphens: none;
}

pre[class*="language-"]::-moz-selection, pre[class*="language-"] ::-moz-selection,
code[class*="language-"]::-moz-selection, code[class*="language-"] ::-moz-selection {
	text-shadow: none;
	// background: #b3d4fc;
	background: rgba($color-secondary-lightest, 0.8);
}

pre[class*="language-"]::selection, pre[class*="language-"] ::selection,
code[class*="language-"]::selection, code[class*="language-"] ::selection {
	text-shadow: none;
	// background: #b3d4fc;
	background: rgba($color-secondary-lightest, 0.8);
}

@media print {
	code[class*="language-"],
	pre[class*="language-"] {
		text-shadow: none;
	}
}

/* Code blocks */
pre[class*="language-"] {
	padding: 1em;
	margin: .5em 0;
	overflow: auto;
}

:not(pre) > code[class*="language-"],
pre[class*="language-"] {
	// background: #f5f2f0;
	background: $color-gray-lightest;
	border: 1px solid $color-gray-plus2;
	border-radius: 2px;
}

/* Inline code */
:not(pre) > code[class*="language-"] {
	padding: .1em;
	border-radius: .3em;
	white-space: normal;
}

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
	// color: slategray;
	color: $color-gray;
}

.token.punctuation {
	// color: #999;
	color: $color-gray;
}

.namespace {
	opacity: .7;
}

.token.property,
.token.tags,
.token.boolean,
.token.number,
.token.constant,
.token.symbol,
.token.deleted {
	// color: #905;
	color: $color-accent-lighter;
}

.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
	// color: #690;
	color: $color-secondary-darker;
}

.token.operator,
.token.entity,
.token.url,
.language-css .token.string,
.style .token.string {
	color: #a67f59;
	background: hsla(0, 0%, 100%, .5);
}

.token.atrule,
.token.attr-value,
.token.keyword {
	// color: #07a;
	color: $color-primary;
}

.token.function {
	// color: #DD4A68;
	color: $color-error-lighter;
}

.token.regex,
.token.important,
.token.variable {
	// color: #e90;
	color: $color-warning;
}

.token.important,
.token.bold {
	font-weight: bold;
}
.token.italic {
	font-style: italic;
}

.token.entity {
	cursor: help;
}
